body {
  background: $background-color;
}

//header layout and anything that relies on the variable header height
header{
  position:relative;
  z-index:99;
}

@media (max-width: 1024px){
  $header-height: 70px;
  header, 
  header [class*='span-'] {
    height:$header-height;
  }
  .main-content, .question-area-container{
    min-height: calc(100vh - #{$header-height});
  }
  // .page-container{
  //   height:calc(100vh - #{$header-height}); //70px is height of header
  // }
}
@media (min-width: 1025px){
  $header-height: 100px;
  header, 
  header [class*='span-'] {
    height:$header-height;
  }
  .main-content, .question-area-container{
    min-height: calc(100vh - #{$header-height});
  }
  // .page-container{
  //   height:calc(100vh - #{$header-height}); //70px is height of header
  // }
}

.equal-width-children{
  display: table;
  table-layout: fixed;
  width:100%;
}

.equal-width-children > *{
  display: table-cell;
}

//set the thing that appears on the left to absolute position
//to take it out of the scope of the header, otherwise it won't 
//center-horizontally
.header-left{
  position:absolute;
  top:1em;
  left:1em;
}

.spacer{
  height:16px;
}

